import React, { Component } from 'react';
import scroll from '../assets/img/scroll-icon.png';
import '../assets/css/Home.css';

class Home extends Component {
  render() {
    return (
      <div className="home">
        <h1>CHEN&nbsp;&nbsp;SONG</h1>
        <h2>Front End Developer</h2>
        <a href="#about"><img src={scroll} className="scroll-down" alt="scroll"/></a>
      </div>
    );
  }
}

export default Home;