import React, { Component } from 'react';
import '../assets/css/Section.css';
import Home from './Home';
import About from './About';
import Work from './Work';
import Contact from './Contact';

class Section extends Component {
  render() {
    return (
      (this.props.section === "home" && <section className="section" id="home"><Home /></section>) ||
      (this.props.section === "about" && <section className="section" id="about"><About /></section>) ||
      (this.props.section === "work" && <section className="section" id="work"><Work /></section>) ||
      (this.props.section === "contact" && <section className="section" id="contact"><Contact /></section>)
    );
  }
}
export default Section;