import React, { Component } from 'react';
import scroll from '../assets/img/scroll-icon.png';
import '../assets/css/About.css';

class About extends Component {
  render() {
    return (
      <div className="about">
        <h1 className="align-left">HERE WHO I AM</h1>
        <p>I am a Front-End Developer and I enjoy building Web UIs with the most popular technologies. I am able to do development, maintenance, and enhancement of large Web applications, and deliver efficient clean code. I have rich experience in designing Single Page Application, Responsive Webpage Application, and solving problems.</p>
        <p>Have great passion of learning new skills.</p>
        <div className="align-left">Proficient in:<br/>
          <span className="highlight-light">HTML, CSS, Javascript, jQuery, AngularJS, ReactJs, Redux, Sass, Bootstrap, Node.js<br/>C, Java</span>
        </div>
        <div className="view-work">
          <span>View My Work</span>
          <a href="#work"><img src={scroll} className="scroll-down" alt="scroll"/></a>
        </div>
      </div>
    );
  }
}

export default About;