import React, { Component } from 'react';
import mySubaru from '../assets/img/my-subaru.svg';
import stateauto from '../assets/img/stateauto.png'
import '../assets/css/Work.css';

class Work extends Component {
  render() {
    return (
      <div className="work">
        <h1>Projects I've Worked</h1>
        <div className="content-block">
          <img src={mySubaru} alt="logo" width="200"/>
          <h1 className="sub-title">MySubaru</h1>
          <p className="highlight-dark">HTML | CSS | Javascript | jQuery | Sass | Bootstrap | Cordova</p>
          <p>MySubaru is a Web and also has a mobile application on phone for Subaru vehicle customers. It makes every part of owning Subaru easy. With MySubaru, all Subaru owners can access vehicle’s features remotely from anywhere including: Start/Stop Engine, Locate vehicle, Lock/Unlock doors, and more. Also, they can schedule services and access account info.</p>
          <a href="https://www.mysubaru.com/" className="button" target="_blank" rel="noopener noreferrer">View Site</a>
        </div>
        <div className="content-block">
          <img src={stateauto} alt="logo" width="200"/>
          <h1 className="sub-title">Gate Portal of State Auto</h1>
          <p className="highlight-dark">HTML | CSS | Javascript | AngularJS | jQuery | Sass | Node.js</p>
          <p>Gate Portal is an application for agents of State Auto Insurance to sell insurance polices. Agents are able to use this to quote, sell, and endorsement insurance policies for their personal or business customers.</p>
        </div>
      </div>
    );
  }
}

export default Work;